const initState={
	authError:null
}


const authReducer=(state=initState,action)=>{
	switch(action.type){
		case 'LOGIN_ERROR':
			console.log('login error');
			return{
				...state,
				authError:'The makevents email or password do not match. Please try again'
			}
		case 'LOGIN_SUCCESS':
			console.log('login success');
			return{
				...state,
				authError:null
			}
		case 'SIGNOUT_SUCCESS':
			return state;

		case 'SIGNUP_SUCCESS':
			return{
				...state,
				authError:null
			}
		case 'SIGNUP_ERROR':
			return{
				...state,
				authError:action.err.message
			}	
		default:
			return state;
	}
}

export default authReducer